@font-face {
  font-family: "KoPubWorld-Batang-Medium";
  font-weight: normal;
  src: url("./fonts/KoPubWorld-Batang-Medium.ttf") format("truetype");
}

body, html {
  margin: 0;
  padding: 0;
}

.app {
  font-family: "KoPubWorld-Batang-Medium";
  overflow-x: hidden;
}

.top-nav-bar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  display: flex;
  background-color: white;
  justify-content: space-around;
  width: 100%;
  border-bottom: 1px solid rgb(200, 200, 200);
}

.top-nav-bar li {
  display: block;
  color: black;
  text-align: center;
  padding: 20px;
  cursor: pointer;
  font-family: "KoPubWorld-Batang-Medium";
}

.bottom-nav-bar {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: white;
}

.bottom-nav-bar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.bottom-nav-choice button {
  display: block;
  color: black;
  width: calc(100% - 50px);
  border: none;
  background-color: white;
  font-size: 16px;
  padding: 12px 20px;
  text-decoration: none;
  border-bottom: 1px solid rgb(161, 161, 161);
  margin-left: 25px;
  text-align: left;
  font-family: "KoPubWorld-Batang-Medium";
  cursor: pointer;
}

.bottom-nav-items button {
  display: block;
  color: white;
  width: 100%;
  font-size: 16px;
  border: none;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-family: "KoPubWorld-Batang-Medium";
  background-color: black;
  cursor: pointer;
}

.bottom-nav-bar ul div {
  text-align: center;
  font-family: "KoPubWorld-Batang-Medium";
}

.typing-effect {
  margin: 0;
  padding: 120px 30px 30px 30px; /* Adjusted padding for better spacing */
  text-align: left;
  word-break: keep-all;
  text-indent: 10px;
  font-family: "KoPubWorld-Batang-Medium";
  line-height: 1.5;
}

::-webkit-scrollbar {
  display: none;
}
